
'use server'; // Mark this if used directly in Server Components/Actions, otherwise remove if only used client-side

import type { GeoLocation } from '@/lib/types';

/**
 * Fetches a human-readable address for given latitude and longitude using the Nominatim API.
 *
 * @param latitude - The latitude of the location.
 * @param longitude - The longitude of the location.
 * @returns A promise that resolves to the display name (address) or null if an error occurs or no address is found.
 * @throws {Error} If the network request fails.
 */
export async function getAddressFromCoordinates(latitude: number, longitude: number): Promise<string | null> {
  // IMPORTANT: Use a custom user agent for Nominatim as per their usage policy.
  // Replace 'YourAppName/Version YourContactInfo' with your actual app details.
  const userAgent = 'FieldTrack/1.0 (your-email@example.com)'; // BE SURE TO REPLACE THIS
  const url = `https://nominatim.openstreetmap.org/reverse?format=jsonv2&lat=${latitude}&lon=${longitude}`;

  console.log(`Fetching address from: ${url}`); // Log the request URL

  try {
    const response = await fetch(url, {
      headers: {
        'User-Agent': userAgent,
        'Accept': 'application/json',
      },
    });

    if (!response.ok) {
        const errorText = await response.text();
        console.error(`Nominatim API error: ${response.status} ${response.statusText}`, errorText);
        // Don't throw here, return null to allow the app to continue
        return `Error fetching address: ${response.statusText}`;
    }

    const data = await response.json();

    console.log('Nominatim API response:', data); // Log the response data

    if (data && data.display_name) {
      return data.display_name;
    } else {
      console.warn('No address found for the given coordinates.');
      return 'Address not found'; // Return a specific string if no address found
    }
  } catch (error: any) {
    console.error('Error fetching address from Nominatim:', error);
    // Return null or an error message string instead of throwing
    // This prevents the entire location fetching process from failing
    return 'Could not fetch address due to network error.';
  }
}
