
import type { GeoLocation } from '@/lib/types';
import { GeolocationError } from '@/lib/errors'; // Import the custom error

/**
 * Asynchronously retrieves the current geographical location using the browser's Geolocation API.
 *
 * @returns A promise that resolves to a GeoLocation object containing latitude and longitude.
 * @throws {GeolocationError} If Geolocation is not supported, permission is denied, the position is unavailable, the request times out, or an unknown error occurs.
 */
export function getCurrentLocation(): Promise<GeoLocation> {
  return new Promise((resolve, reject) => {
    if (!navigator.geolocation) {
      // Use GeolocationError for consistency, code 0 can represent 'not supported'
      reject(new GeolocationError('Geolocation is not supported by your browser.', 0));
      return;
    }

    navigator.geolocation.getCurrentPosition(
      (position) => {
        resolve({
          latitude: position.coords.latitude,
          longitude: position.coords.longitude,
          // address will be fetched separately
        });
      },
      (error) => {
        let message: string;
        switch (error.code) {
          case error.PERMISSION_DENIED: // Code 1
            message = 'Location permission denied. Please allow location access in your browser/system settings.';
            break;
          case error.POSITION_UNAVAILABLE: // Code 2
            message = 'Location information is unavailable. Check your network connection or GPS signal.';
            break;
          case error.TIMEOUT: // Code 3
            // Refined message for timeout
            message = 'Could not get location in time. Check GPS signal and network connection, then try again.';
            break;
          default: // Includes error.UNKNOWN_ERROR and any other codes
            message = 'An unknown error occurred while retrieving location.';
            break;
        }
        // Reject with the custom error, including the original code and a detailed message
        // The error originates here when the timeout occurs, but this is expected behavior.
        reject(new GeolocationError(`${message} (Code: ${error.code})`, error.code));
      },
      {
        enableHighAccuracy: true, // Request more accurate position
        timeout: 30000, // Increased timeout to 30 seconds
        maximumAge: 0, // Force fresh location data
      }
    );
  });
}
